// ** React Imports
import { Link } from 'react-router-dom'

// ** Store & Actions
import { store } from '@store/storeConfig/store'

// ** Third Party Components
import { Badge, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap'
import {MoreVertical, FileText, Trash2, Archive, Image} from 'react-feather'
import {deleteSocialStory, getSocialStory, statusSocialStory, updateSocialStory} from "../store/action"
import Avatar from "../../../@core/components/avatar"

// ** Renders Client Columns
const renderAvatarImage = row => {
  const stateNum = Math.floor(Math.random() * 6),
      states = ['light-success', 'light-danger', 'light-warning', 'light-info', 'light-primary', 'light-secondary'],
      color = states[stateNum]
  if (row.avatar_url_thumb && row.avatar_url_thumb !== '') {
    return <Avatar className='m-auto avatar' img={row.avatar_url_thumb} width='100' height='100' />
  } else {
    return <Avatar color={color || 'primary'} className='m-auto avatar' content={row?.name || 'FEAER'} initials />
  }
}

const renderImage = row => {
  if (row?.image_url_thumb) {
    return <img className='mr-1' src={row.image_url_thumb} width='70' height='70' />
  }
}

const statusObj = {
  true: 'light-success',
  false: 'light-secondary'
}

function updateStatus(row) {
  row.is_active = !row.is_active
  store.dispatch(statusSocialStory(row, row.id))
}

export const columns = [
  {
    name: 'Avatar',
    minWidth: '40px',
    selector: 'fullName',
    sortable: true,
    cell: row => renderAvatarImage(row)
  },
  {
    name: 'Tên instagram',
    minWidth: '40px',
    selector: 'fullName',
    sortable: true,
    cell: row => (
        <div className='d-flex justify-content-left align-items-center'>
          <div className='d-flex flex-column'>
            <Link
                to={`/instagram/detail/${row.id}`}
                className='user-name text-truncate mb-0'
                onClick={() => store.dispatch(getSocialStory(row.id))}>
              <span className='font-weight-bold'>{row.name}</span>
            </Link>
          </div>
        </div>
    )
  },
  {
    name: 'Hình ảnh review sản phẩm',
    minWidth: '100px',
    sortable: true,
    cell: row => renderImage(row)
  },
  {
    name: 'Đường dẫn instagram',
    minWidth: '300px',
    selector: 'name',
    sortable: true,
    cell: row => row?.social_link
  },
  {
    name: 'Ngày tạo',
    minWidth: '320px',
    selector: 'email',
    sortable: true,
    cell: row => row?.createdAt
  },
  {
    name: 'Trạng thái',
    minWidth: '138px',
    selector: 'status',
    sortable: true,
    cell: row => (
        <Badge className='text-capitalize' color={statusObj[row.is_active]} pill>
          {row?.is_active ? 'Sử dụng' : 'Tạm ngưng'}
        </Badge>
    )
  },
  {
    name: 'Hành động',
    minWidth: '100px',
    cell: row => (
        <UncontrolledDropdown>
          <DropdownToggle tag='div' className='btn btn-sm'>
            <MoreVertical size={14} className='cursor-pointer' />
          </DropdownToggle>
          <DropdownMenu right>
            <DropdownItem
                tag={Link}
                to={`/instagram/detail/${row.id}`}
                className='w-100'
                onClick={() => store.dispatch(getSocialStory(row.id))}>
              <FileText size={14} className='mr-50' />
              <span className='align-middle'>Chi tiết</span>
            </DropdownItem>
            <DropdownItem className='w-100' onClick={() => updateStatus(row)}>
              <Archive size={14} className='mr-50' />
              {
                row?.is_active &&
                <span className='align-middle' >Tắt sử dụng</span>
              }
              {
                !row?.is_active &&
                <span className='align-middle' >Sử dụng</span>
              }
            </DropdownItem>
            <DropdownItem className='w-100' onClick={() => store.dispatch(deleteSocialStory(row.id))}>
              <Trash2 size={14} className='mr-50' />
              <span className='align-middle'>Xóa</span>
            </DropdownItem>
          </DropdownMenu>
        </UncontrolledDropdown>
    )
  }
]
